<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Material',query:{typeName,index:0}}">教学大纲</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'MaterialOutLine',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div id="introduce" class="introduce">
          <h1 class="titleBig">《数据库原理及应用》课程教学大纲</h1>
          <h2 class="titleSmall">一、课程概况</h2>
          <p class="contentSmall">
            本课程是在《动物遗传学》和《生物统计学》课程基础上，在动物科学专业课程体系中起承上启下作用。一方面它与前期所学生物统计学、动物遗传学密切相关，另一方面又是后期动物生产各论课的铺垫。本课程主要内容包括两大部分：选种和选配。通过本课程的学习，使学生掌握家畜育种的基本原理和方法，育种措施的设计和优化，育种技术的新发展及运用和家畜遗传资源的保护与利用等。为学生将来从事动物育种、动物生产和动物保护等相关行业奠定基础。
          </p>
          <h2 class="titleSmall">二、课程简介</h2>
          <p class="contentSmall">
            《家畜育种学》是动物科学专业的专业基础课。本课程在动物科学专业课程体系中起承上启下作用，本课程主要围绕数量性状的选择原理和方法，杂种优势利用，选配等内容，让学生学习品种、品系培育方法以及动物遗传资源保护基本原理，培养学生灵活运用育种理论和措施解决生产中实际问题的能力。
          </p>
          <h2 class="titleSmall">三、课程性质</h2>
          <p class="contentSmall">
            《家畜育种学》是动物科学专业的专业基础课。本课程在动物科学专业课程体系中起承上启下作用，本课程主要围绕数量性状的选择原理和方法，杂种优势利用，选配等内容，让学生学习品种、品系培育方法以及动物遗传资源保护基本原理，培养学生灵活运用育种理论和措施解决生产中实际问题的能力。
          </p>
          <h2 class="titleSmall">四、课程教学目标</h2>
          <p class="contentSmall">
            本课程要求学生掌握家畜育种学的基本概念和原理，包括数量性状选择原理，育种值估计原理，近交与远交的遗传效应及用途，杂种优势产生的原理和利用，品种与品系培育方法。要求学生熟悉各种常见家畜品种的特征和生产力类型，熟练掌握系谱编制、综合选择指数制订、近交系数与亲缘系数制订、个体选配等必备的家畜育种措施；能根据实际生产需求设计育种方案并确定相关的育种措施。通过本课程的学习，使学生具备运用育种理论和措施解决生产中实际问题的能力。
          </p>
          <h2 class="titleSmall">五、理论教学内容及要求</h2>

          <h3 class="titleSmallSmall">第0章 &nbsp;&nbsp;绪论</h3>
          <h3 class="titleSmallSmall">【教学目标】</h3>
          <p class="contentSmall">
            （1）了解：家畜育种学的发展历程
          </p>
          <p class="contentSmall">
            （2）理解：家畜育种在发展畜牧业中的作用
          </p>
          <p class="contentSmall">
            （3）掌握：家畜育种学的任务和内容
          </p>

          <h3 class="titleSmallSmall">
            【学时分配】
            <span class="titleSpan">
              1学时
            </span>
          </h3>

          <h3 class="titleSmallSmall">
            【授课方式】
            <span class="titleSpan">
              讲授1学时
            </span>
          </h3>

          <h3 class="titleSmallSmall">【课外学习指导的要求】</h3>
          <p class="contentSmall">
            1.课外阅读资料：动物遗传育种讨论会文集；
          </p>
          <p class="contentSmall">
            2.作业与思考题：家畜育种学的概念
          </p>

          <h3 class="titleSmallSmall">【教学重点和难点】</h3>
          <p class="contentSmall">
            （1）重点：家畜育种学的任务和内容
          </p>
          <p class="contentSmall">
            （2）难点：无
          </p>

          <h3 class="titleSmallSmall">【授课内容】</h3>
          <p class="contentSmall">
            第一节 家畜育种学的定义和研究内容
            <span class="contentSmallSpan">
              1. 家畜育种学的定义
            </span>
            <span class="contentSmallSpan">
              2. 家畜育种学研究的内容
            </span>
          </p>
          <p class="contentSmall">
            第二节 家畜育种学发展简史及成就
            <span class="contentSmallSpan">
              1. 家畜育种学的诞生
            </span>
            <span class="contentSmallSpan">
              2. 家畜育种学的发展
            </span>
          </p>
          <p class="contentSmall">
            第三节  家畜育种学与动物生产的关系
            <span class="contentSmallSpan">
              1. 家畜育种学技术在动物生产中的应用
            </span>
            <span class="contentSmallSpan">
              2.展望
            </span>
          </p>

          <h3 class="titleSmallSmall">【授课方法与手段】</h3>
          <p class="contentSmall">
            （1）教学方法：采用讲授式教学方法，兼有课堂提问。
          </p>
          <p class="contentSmall">
            （2）教学手段：采用多媒体教学，使学生对抽象的内容有一定感性认识，指导学生了解与本门课程教学相关的主要网站。
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //评价数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Material',     //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示评价
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示介绍
      setIntroduce(id,videoIntro){
        if(videoIntro){
          $('#'+id).html(videoIntro)
        }else{
          $('#'+id).html('此栏目下没有数据')
        }
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index} = this
        this.file = List[index]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Material',
          menuName:'教学大纲',
          name:'MaterialOutLine'
        },
        {
          typeName:'Material',
          menuName:'教学目标',
          name:'MaterialDetail'
        },
        {
          typeName:'Material',
          menuName:'教学内容组织形式',
          name:'MaterialForm'
        },
      ],      //目录
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "教学大纲",
          "content":'',
        },
        {
          "id": "1",
          "courseName": "教学目标",
          "content":'本课程要求学生掌握家畜育种学的基本概念和原理，包括数量性状选择原理，育种值估计原理，近交与远交的遗传效应及用途，杂种优势产生的原理和利用，品种与品系培育方法。要求学生熟悉各种常见家畜品种的特征和生产力类型，熟练掌握系谱编制、综合选择指数制订、近交系数与亲缘系数制订、个体选配等必备的家畜育种措施；能根据实际生产需求设计育种方案并确定相关的育种措施。通过本课程的学习，使学生具备运用育种理论和措施解决生产中实际问题的能力。',
        },
        {
          "id": "2",
          "courseName": "教学内容组织形式",
          "content":'',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          .introduce{
            color: #333;
            .titleBig{
              text-align: center;
              font-weight: bold;
              font-size: 24px;
              margin: 10px 0 30px;
            }
            .titleSmall{
              font-weight: bold;
              font-size: 18px;
              margin: 20px 0 10px;
            }
            .titleSmallSmall{
              font-size: 16px;
              font-weight: bold;
              margin: 10px 0;
              .titleSpan{
                text-indent: 2em;
                font-size: 14px;
                line-height: 24px;
                font-weight: normal;
              }
            }
            .contentSmall{
              text-indent: 1em;
              font-size: 14px;
              line-height: 24px;
              margin-bottom: 5px;
              .contentSmallSpan{
                display: block;
                text-indent: 2em;
                font-size: 14px;
                line-height: 24px;
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .introduce {
              .titleBig {
                font-size: 20px;
              }
            }
          }
        }
      }
    }
  }
</style>



